.container {
  display: grid;
  height: 100vh;
  place-items: center;
  overflow: hidden;
  --circleDiameter: 300px;
  --planets: calc(var(--circleDiameter) / 5);
}

.social-icon {
  border: 5px solid #1976d2;
  border-radius: 50%;
  animation: scale 1s linear infinite alternate;
  display: flex;
  justify-content: center;
  align-items: center;
}

.social-link {
  color: inherit;
  text-decoration: none;
}

[data-theme="light"] .social-icon {
  background-color: white;
}

[data-theme="dark"] .social-icon {
  background-color: black;
}

.social-icon > svg {
  width: 100%;
  height: 80%;
}

.hero-circle {
  position: relative;
  width: var(--circleDiameter);
  height: var(--circleDiameter);
}
.inner-img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  overflow: hidden;
  border-radius: 50%;
  width: 80%; /* Adjusted width */
  height: 80%; /* Adjusted height */
  max-width: 200px; /* Added max-width for limiting size on smaller screens */
  max-height: 200px; /* Added max-height for limiting size on smaller screens */
}

.hero-rotate {
  position: relative;
  width: 100%;
  height: 100%;
  border: 7px solid #1976d2;
  border-radius: 100%;
  animation: rotate 55s linear infinite;
}

.planet {
  position: absolute;
  width: var(--planets);
  aspect-ratio: 1;
  border-radius: 100%;
  animation: maintain 30s linear infinite;
}

.planet:nth-child(1) {
  top: calc(-1 * var(--planets) / 2);
  left: calc(var(--circleDiameter) / 2 - var(--planets) / 2 - 5px);
}

.planet:nth-child(2) {
  top: calc(var(--circleDiameter) / 2 - var(--planets) / 2 - 5px);
  right: calc(-1 * var(--planets) / 2);
}

.planet:nth-child(3) {
  top: calc(var(--circleDiameter) / 2 - var(--planets) / 2 - 5px);
  left: calc(-1 * var(--planets) / 2);
}

.planet:nth-child(4) {
  left: calc(var(--circleDiameter) / 2 - var(--planets) / 2 - 5px);
  bottom: calc(-1 * var(--planets) / 2);
}

@media screen and (max-width: 395px) {
  .container {
    --circleDiameter: 230px;
    --planets: calc(var(--circleDiameter) / 5);
  }
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes maintain {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(-360deg);
  }
}

@keyframes scale {
  0% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

.mouse-icon {
  border-radius: 16px;
  height: 40px;
  width: 24px;
  display: block;
  z-index: 10;
  opacity: 0.7;
}

.mouse-icon .wheel {
  position: relative;
  border-radius: 10px;
  width: 2px;
  height: 6px;
  top: 4px;
  margin-left: auto;
  margin-right: auto;
}

/* Dark Theme */
[data-theme="dark"] .mouse-icon {
  border: 2px solid #fff; /* Set border color for dark theme */
}

/* Light Theme */
[data-theme="light"] .mouse-icon {
  border: 2px solid #000; /* Set border color for light theme */
}

[data-theme="dark"] .mouse-icon .wheel {
  border: 2px solid #fff; /* Set border color for dark theme */
}

/* Light Theme */
[data-theme="light"] .mouse-icon .wheel {
  border: 2px solid #000; /* Set border color for light theme */
}

.mouse-icon .wheel {
  -webkit-animation-name: drop;
  -webkit-animation-duration: 1s;
  -webkit-animation-timing-function: linear;
  -webkit-animation-delay: 0s;
  -webkit-animation-iteration-count: infinite;
  -webkit-animation-play-state: running;
  -webkit-animation-name: drop;
  animation-name: drop;
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-timing-function: linear;
  animation-timing-function: linear;
  -webkit-animation-delay: 0s;
  animation-delay: 0s;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  -webkit-animation-play-state: running;
  animation-play-state: running;
}

@-webkit-keyframes drop {
  0% {
    top: 5px;
    opacity: 0;
  }
  30% {
    top: 10px;
    opacity: 1;
  }
  100% {
    top: 25px;
    opacity: 0;
  }
}
@keyframes drop {
  0% {
    top: 5px;
    opacity: 0;
  }
  30% {
    top: 10px;
    opacity: 1;
  }
  100% {
    top: 25px;
    opacity: 0;
  }
}
