.vertical-tabs-container {
  position: fixed;
  top: 0;
  right: 0;
  height: 100vh; /* Adjust height as needed */
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.vertical-tabs {
  cursor: pointer;
}

/* Popup styles */
.vertical-tabs-popup {
  display: flex;
  flex-direction: column;
  justify-content: center; /* Center vertically */
  align-items: center; /* Center horizontally */
  position: relative;
  transition: transform 0.3s ease-in-out;
}

/* Arrow styles */
.arrow {
  display: none;
  cursor: pointer;
}

/* Media query for smaller screens */
@media (max-width: 600px) {
  .vertical-tabs-container {
    position: static;
    height: auto;
  }

  .vertical-tabs-popup {
    position: fixed;
    top: 50%;
    right: -3px;
    transform: translateX(100%);
    background-color: var(--popup-background);
    box-shadow: -2px 0 5px #1976d2;
    z-index: 1000;
    border-radius: 5px 0 0 5px;
  }

  .vertical-tabs-popup.open {
    transform: translateX(0);
  }

  .arrow {
    display: block;
    position: absolute;
    left: -2px;
    background-color: var(--popup-background);
    padding: 6px;
    border-radius: 5px 0 0 5px;
    box-shadow: -2px 0 5px #1976d2;
    animation: moveArrow 1s infinite alternate; /* Horizontal moving animation */
    transform: rotate(180deg); /* Initially rotated 180 degrees */
  }

  .vertical-tabs-popup.open .arrow {
    transform: rotate(0deg); /* Rotate to 0 degrees when container is open */
  }

  @keyframes moveArrow {
    from {
      left: -20px; /* Start position */
    }
    to {
      left: -24px; /* End position */
    }
  }
}

/* Dark theme */
[data-theme="dark"] {
  --popup-background: #121212;
}

/* Light theme */
[data-theme="light"] {
  --popup-background: white;
}
